* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #0B2434;
  padding:0 20px;
  font-family: 'Karla', sans-serif;
  display: grid;
  place-content: center;
  width: 100vw;
  min-height: 100vh;
}

main {
  background-color: #F5F5F5;
  height: 400px;
  max-width: 800px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  /* margin: 40px auto; */
}

.title {
  font-size: 40px;
  margin: 0;
}

.instructions {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}
.instructions-two {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}

.die-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 0px 6px;
  display: flex;
  justify-content: center;
  align-items: center;

}
.die-dots {
  display: flex;
  height: 40px;
  width: 40px;
  margin: auto;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  cursor: pointer;
  /* background-color: yellow; */
}
.die-num {
  font-size: 2rem;
}

.roll-dice {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  font-size: 1.2rem;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
}

.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

span {
  background-color: black;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 0 2px;
  /* font-size: 0.4rem; */
  /* 
   Why is there space between them vertically
  */
}


@media (max-width : 400px) {
    .dice-container {
      gap: 20px;
    }
    .die-face {
      height: 30px;
      width: 30px;
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      font-size: 0.8rem;
    }
    .die-num {
      font-size: 1rem;
    }

}